.projects {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.projects .intro {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    grid-gap: 3vw;
    gap: 3vw;
    margin-left: 3vw;
    margin-bottom: 1vh;
}

button {
    border: 1px solid rgba(0, 0, 0, 0.158);
    background: rgba(0, 26, 255, 0.226);
    border-radius: 7px;
    padding: 0.35vw;
}

button:hover {
    cursor: pointer;
    opacity: 0.90;
}

.projects-data {
    display: flex;
    justify-content: flex-start;
    margin: 0 2vw;
    grid-gap: 0.3vw;
    gap: 0.3vw;
}

.project {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  
 li {
    list-style-type: none;
  }
  
  .project .project-info {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    grid-gap: 1.5vh;
    gap: 1.5vh;
    border: 1px solid white;
    background: rgba(193, 222, 255, 0.247);
    border-radius: 15px;
    padding: 2vh;
  }

  .project .project-info li {
    display: flex;
    flex-direction: row;
    grid-gap: 0.5vw;
    gap: 0.5vw;
  }

  .project .project-info li p {
    margin: 0;
  }
  
  .project .project-info h3 {
    margin: 0;
    margin-bottom: 0.5vh;
  }

  .participant-list {
    position: absolute;
    max-height: 100px;
    padding: 1vh 50px;
    background: white;
    cursor: pointer;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    grid-gap: 1vh;
    gap: 1vh;
  }

  .delete-project-button {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: flex-end;
    margin: 0;
  }

  .delete-project-button h3 {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .delete-project-button button {
    background: #ffd2d2;
  }

  .delete-project-button img {
    height: 15px;
  }


h1 {
    text-align: center;
    margin: 3vh 0;
}

.projects-container h1 {
    background: #242424;
    margin: 0;
    padding: 3vh 0;
    color: white;
}

* {
    margin: 0;
    padding: 0;
}

.accueil-container {
    scroll-behavior: smooth;
}

.accueil {
    display: flex;
    flex-direction: row;
    width: 100%;
    background: #00242f;
}

.accueil::after {
    content: "";
    height: 10px;
    background: #030303;
}

img[src='img/logo-dark.png'], img[src='img/logo.png'], img[src='../../../../img/logo-dark.png'] {
    margin: 0;
    height: 92px;
    position: absolute;
}

.accueil .naviguation {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.accueil .naviguation a {
    color: white;
}

.projects-bar {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    position: fixed;
    top: 15px;
    right: 15px;
}

.projects-bar img {
    height: 35px;
    width: 45px;
    cursor: pointer;
    position: fixed;
    right: 15px;
}

.projects-bar ul {
    display: flex;
    flex-direction: column;
    grid-gap: 30px;
    gap: 30px;
    background: #0d3a55;
    padding: 30px;
    margin-top: 50px;
}

.projects-bar ul li {
    list-style-type: none;
}

.projects-bar ul a {
    text-decoration: none;
    color: white;
    font-size: 1.1em;
}

.presentation {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    height: 80vh;
}

.presentation video {
    width: 90vw;
    height: 60vh;
}

.projects-accueil {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
    margin:0;
    width: 100vw;
    background: #242424;
    padding: 5vh 0;
}

.projects-accueil .project {
    display: flex;
    flex-direction: column;
    width: 350px;
    background: rgb(32, 30, 30);
    color: white;
    border-radius: 15px;
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 1vh 2vw;
    grid-gap: 2vh;
    gap: 2vh;
    box-shadow: 0px 0px 15px -12px #FFFFFF;
    transition: all ease-in 0.2s;
}

.projects-accueil .project:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.projects-accueil .project img {
    width: 90%;
}

.projects-accueil .project button {
    all: unset;
    border-radius: 7px;
    background: #ffffff;
    color: black;
    padding: 5px;
    transition: all ease-in-out 0.1s;
}

.projects-accueil .project button:hover {
    cursor: pointer;
    background: rgb(0, 68, 255);
    color: white;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.citation {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 3vh;
    font-size: 20px;
}
body, html {
    margin: 0;
    padding: 0;
}

.chat-container {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    padding: 10px;
    box-sizing: border-box;
    font-family: sans-serif;
    padding: 0;
    background-color: #202124;
  }

  .chat-container .naviguation a {
    color: white;
}

.chat-container .naviguation {
  height: 100px;
}
  
  .chat-form {
    position: fixed;
    bottom: 0;
    display: flex;
    align-items: center;
    flex-direction: row;
    grid-gap: 2vw;
    gap: 2vw;
    background-color: #28292c;
    width: 100vw;
    margin: 0;
    padding: 0;
  }
  
  .chat-input {
    flex: 1 1;
    padding: 8px;
    border-radius: 4px;
    border: none;
    background-color: #2e2e2e;
    color: #fff;
    margin: 0;
    margin:  3vw 0 3vw 3vw;
  }
  
  .chat-button {
    padding: 8px 16px;
    background-color: #7289da;
    color: #fff;
    border: none;
    border-radius: 4px;
    margin-left: 10px;
    cursor: pointer;
    margin: 0;
    margin: 0 3vw 0 0;
  }
  
  .chat-button:hover {
    background-color: #677bc4;
  }
  
.messages-online-users-container {
  display: flex;
  flex-direction: row;
}


.chat-messages {
  flex: 1 1;
  max-height: 80vh;
  overflow-y: scroll;
  color: white;
  display: flex;
  flex-direction: column;
  grid-gap: 2vh;
  gap: 2vh;
  scroll-behavior: smooth;
}


.chat-message {
  color: white;
  background: #1b1b1b;
  padding: 10px;
  display: flex;
  flex-direction: column;
  grid-gap: 1vw;
  gap: 1vw;
}

.chat-author {
  font-weight: bold;
  white-space: nowrap;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}


.chat-content {
  word-break: break-word;
  line-height: 1.5;
}

.chat-message button {
  background: rgb(255, 183, 183);
  height: 18px;
  width: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chat-message img {
  height: 13px;
}

.online-users {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  grid-gap: 3vh;
  gap: 3vh;
  padding: 0 10px;
  background: #0f0f0f;
  height: 100vh;
  overflow-y: scroll;
}

.online-users .user {
  display: flex;
  flex-direction: row;
  grid-gap: 2vw;
  gap: 2vw;
  justify-content: flex-end;
  font-size: 0.7rem;
}

.online-users .user .online-status {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #01c75a;
}


body {
    margin: 0;
    padding: 0;
    background: rgb(250, 250, 250);
}

.naviguation {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: 3vw;
}

.naviguation ul {
    padding: 0;
}

.naviguation a {
    text-decoration: none;
    color: black;
    font-size: 17px;
}

.naviguation li {
    list-style-type: none;
    display: inline;
    margin: 3vw;
    justify-content: center;
}

.form {
    height: calc(100vh - 200px);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
}

form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    border: rgba(0, 0, 0, 0.26) solid 2px;
    border-radius: 11px;
    padding: 20px 40px;
    background: rgba(255, 255, 250, 0.479);
    margin: 0;
}

form input, form select {
    all: unset;
    background: white;
    padding: 10px 20px;
    margin-bottom: 1.8vh;
    box-shadow: 1.1px 1.1px 1px rgb(0, 0, 0);
    border-radius: 5px;
}

form input[type="submit"]:hover {
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

.dashboard-nav-bar, .register-login-nav-bar {
    display: flex;
    flex-direction: row;
    background: #00242f;
    color: white;
}

.dashboard-nav-bar img, .register-login-nav-bar img {
    height: 92px;
}

.dashboard-naviguation, .register-login-naviguation {
    width: 92vw;
}

.dashboard-naviguation, .register-login-naviguation a {
    color: white;
}

.dashboard-naviguation, .register-login-naviguation ul {
    margin-left: 5vw;
}

.profil {
    display: flex;
    flex-direction: column;
}

.profil-avatar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.profil img {
    margin-top: 5px;
    margin-right: 5px;
    height: 40px;
    cursor: pointer;
}

.profil-list {
    display: none;
    position: absolute;
    top: 40px;
    right: 0;
    background: #8974ff11;
}

.profil-list ul {
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    grid-gap: 3vh;
    gap: 3vh;
    padding: 2vh 2vh;
}

.selected-participant img {
    height: 10px;
}

.selected-participant {
    display: flex;
    grid-gap: 0.5vw;
    gap: 0.5vw;
}

.selected-participant button {
    all: unset;
    cursor: pointer;
}

